
import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import store from '../redux/store'

// import { getItems,  getTotalPrice, Update} from '../redux/actions/itemsAction'
import axios from 'axios'
import { getItems, Update,getTotalPrice } from '../redux/actions/itemsActions';

class Table extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            alertShow: false,
            items: null,
      
        }
    }

    componentDidMount() {
        if(this.props.items) {
            axios.get("http://localhost:8084/api/v1/items/?sort=date:desc",
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('jwt')}`
                    }
                })
                .then(res => {
                    store.dispatch(getItems(res.data));
                    let totalPrice = 0;
                    for (let i = 0; i < res.data.length; i++) {
                        totalPrice += parseInt(res.data[i].price)
                    }
                    store.dispatch(getTotalPrice(totalPrice));
                })
                .catch(err => {
                    console.log(err);
                })
        }
    }

    componentDidUpdate() {
        if (this.props.Update) {
                axios.get("http://localhost:8084/api/v1/items/?sort=date:desc",
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('jwt')}`
                        }
                    }
                )
                    .then(res => {
                        store.dispatch(getItems(res.data));
                        store.dispatch(Update(false));
                    })
                    .catch(err => {
                        console.log(err);
                    })
        }
    }

    hideAlert = () => {
        this.setState({ alertShow: false })
    }



   

    render() {

        let itemsList = null;
        if (this.props.items) {
          itemsList = this.props.items.map(item => {
            
            return (
    
              <tr key={item._id }>
                <td>{item.product_name}</td>
                
                <td>{item.product_type}</td>
                <td>{item.product_description}</td>
                <td>{item.purchase_date}</td>
                <td>{item.product_price}</td>
                <td>
                <Link to={`/updateproduct/${item._id}`}>
                <span id="edit" className="btn btn-light" onClick={() => { this.doneEdit(item._id) }}> Edit </span>
              </Link>

            
                <button id="delete" className="btn btn-danger" onClick={()=>{this.onDeleteClick(item._id)}}> Delete </button>
            
            </td>
          </tr >
        );
      });
    }
      
            
       

      
        return (
            <React.Fragment>
        <div className="main-div5">

          <h3>Products</h3>
          <label>Filter by:
<select id="filter5">
              <option>Year</option>
              <option>Highest Price</option>
              <option>Lowest Price</option>
              <option>Latest Purchases</option>
            </select>
          </label>
        </div>

        <table className="table table-dark">
          {this.state.showModal}
          <thead>
            <tr>

              <th>Product name</th>
              <th>Product type</th>
              <th>Product description</th>
              <th>Purchase date</th>
              <th>Product price </th>
              <th></th>
              <th></th>


            </tr>
          </thead>
          <tbody>{itemsList}</tbody>
        </table>
        <Link to="/newproduct">
          <button className="fixed-button5" onClick={this.toggle}>New Product</button>
        </Link>
      </React.Fragment >
    );
  }
}

function mapStateToProps(state) {
    return {
        items: state.items,
        // expensesClicked: state.expensesClicked,
        Update: state.Update
    }
}

export default connect(mapStateToProps)(Table)